import type { auth } from "@repo/backend/convex/betterAuth/auth";
import {
  anonymousClient,
  apiKeyClient,
  inferAdditionalFields,
  organizationClient,
} from "better-auth/client/plugins";
import { createAuthClient } from "better-auth/react";

// Only add convexClient plugin if Convex is configured
const plugins = [
  inferAdditionalFields<typeof auth>(),
  anonymousClient(),
  organizationClient(),
  apiKeyClient(),
];

// Convex plugin removed to avoid module format conflicts
// If Convex integration is needed later, it should be added as a separate optional dependency

export const authClient = createAuthClient({
  plugins,
});
